@use "sass:list";

.bsa-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  left: var(--bsa-sidebar-width);
  right: 0;
  height: var(--bsa-header-height);

  // background: var(--bsa-header-bg);
  background-color: var(--#{$prefix}body-bg);
  border-bottom: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
  z-index: 80;
  list-style: none;
  margin: 0;
  padding: 0 12px;

  // left0
  @include media-breakpoint-down(lg) {
    left: 0;
    padding: 0;

    .bsa-search-form-wrapper {
      display: none;

      &.open {
        border-bottom: var(--#{$prefix}border-width) solid var(--#{$prefix}border-color);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: var(--bsa-header-height);
        padding: 0 12px;
        z-index: 10;
        display: flex;
        align-items: center;
        background-color: var(--#{$prefix}body-bg);
        width: 100%;

        .bsa-search-form {
          width: 100%;
        }

      }
    }
  }


  // li
  > li {
    height: inherit;
  }


  .dropdown {
    // lg
    @include media-breakpoint-down(lg) {
      position: static !important;
    }
  }

  // 
  .dropdown-menu {
    border: 0;
    border-radius: 10px;

    // 
    box-shadow: 0 .125rem .25rem rgba(var(--bs-secondary-color-rgb), .075);
    max-width: 360px;

    @include media-breakpoint-down(lg) {
      min-width: 100%;
    }

    // 
    .card {
      border: 0;
      min-width: 300px;
      max-height: 280px;


      @include media-breakpoint-down(lg) {
        min-width: 100%;
      }

      .card-body {
        overflow: hidden;
      }
    }

    // item
    .dropdown-item {
      display: block;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding: 8px 20px;
    }

  }

  // 
  .bsa-sidebar-toggler {
    display: none;

    // lgtoggler
    @include media-breakpoint-down(lg) {
      display: block;
    }
  }

  // 
  .bsa-search-form-toggler {
    display: none;

    // lg
    @include media-breakpoint-down(lg) {
      display: inline;
    }
  }


  // 
  .bsa-search-form {
    position: relative;
    width: 240px;

    // 
    .form-control {
      padding-right: 44px;
      padding-left: 20px;
      border-radius: 30px;

      &:focus {
        box-shadow: none;
        border-color: var(--#{$prefix}border-color);
      }
    }

    // 
    .bsa-search-close-btn,
    .bsa-search-submit-btn {
      font-size: 18px;
      padding: 0;
      border: 0;
      height: inherit;
      width: 24px;
      background-color: transparent;
      color: var(--#{$prefix}body-color);
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);
    }

    // 
    .bsa-search-submit-btn {
      // lg
      @include media-breakpoint-down(lg) {
        display: none;
      }
    }

    // pc
    .bsa-search-close-btn {
      display: none;

      // lg
      @include media-breakpoint-down(lg) {
        display: inline;
      }
    }

  }

}


// 
.bsa-theme-switcher-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 1rem;

  input[type="checkbox"] {
    cursor: pointer;
    position: relative;
    width: 30px;
    height: 30px;
    font-size: .9rem;
    visibility: hidden;

    &::after {
      position: absolute;
      background-color: #ccc;
      width: 30px;
      height: 30px;
      visibility: visible;
      content: " ";
      border: var(--bs-border-width) solid var(--bs-border-color);
      border-radius: 50%;
    }

    &:checked::after {
      content: "";
      color: #fff;
      font-size: 1.25rem;
      font-weight: 700;
      display: flex;
      align-items: center;
      justify-content: center;
    }


    &[value="light"]::after {
      background-color: #fff;
    }

    &[value="light"]:checked::after {
      color: var(--bs-body-color);
    }

    &[value="dark"]::after {
      background-color: #212529;
    }

    &[value="indigo"]::after {
      background-color: #6610f2;
    }

    &[value="green"]::after {
      background-color: #198754;
    }

    &[value="blue"]::after {
      background-color: #0d6efd;
    }

    &[value="yellow"]::after {
      background-color: #ffc107;
    }

    &[value="orange"]::after {
      background-color: #fd7e14;
    }

    &[value="teal"]::after {
      background-color: #20c997;
    }

    &[value="cyan"]::after {
      background-color: #0dcaf0;
    }

    &[value="pink"]::after {
      background-color: #d63384;
    }

    &[value="red"]::after {
      background-color: #dc3545;
    }
  }
}

.bsa-header-item {
  cursor: pointer;
  background: inherit;
  font-size: 24px;
  padding: 0 20px;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;

  // color: var(--bsa-header-item-color);
  color: var(--#{$prefix}body-color);

  // lgitem
  @include media-breakpoint-down(lg) {
    padding: 0 20px;
    font-size: 18px;
  }

  @include media-breakpoint-down(md) {
    padding: 0 16px;
  }

  @include media-breakpoint-down(sm) {
    padding: 0 12px;
  }


  @include media-breakpoint-down(three-four-two) {
    padding: 0 8px;
  }


  // 
  .bsa-user-area {
    display: flex;
    align-items: center;

    // 
    .bsa-user-avatar {
      height: 40px;
      width: 40px;
      border-radius: 50%;
      overflow: hidden;
      flex-shrink: 0;
      user-select: none;

      // lg
      @include media-breakpoint-down(lg) {
        height: 35px;
        width: 35px;
      }

      // 342
      @include media-breakpoint-down(three-four-two) {
        height: 30px;
        width: 30px;
      }

    }


    // 
    .bsa-user-details {
      margin-left: 8px;
      flex-shrink: 0;
      display: block;
      max-width: 150px;

      // xl
      @include media-breakpoint-down(xl) {
        display: none;
      }
    }
  }
}
